.follow {
  padding: 60px 0;
  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-bottom: 45px;
  }

  &__list {
    display: flex;
    gap: 25px;
  }

  &__icon {
    width: 32px;
    height: 32px;
    fill: var(--color-gray);
    transition: 0.5s;
    opacity: 1;
    &:hover {
      fill: var(--color-primary);
    }
  }
}
