.header {
  padding: 25px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  &__container {
    display: flex;
    align-items: center;
    gap: 60px;
    justify-content: space-between;
  }
  &--bg {
    background-color: var(--color-white);
  }
  &__content {
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: 0.4s;
    @include tablet {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      background-color: var(--color-white);
      justify-content: center;
      flex-direction: column;
      z-index: 500;
      transform: translateX(-100%);
    }
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 88px;
    @include tablet {
      flex-direction: column;
      justify-content: center;
      gap: 20px;
    }
  }

  &__social-item {
    display: flex;
    flex-direction: column;
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 40px 40px;
    padding-left: 60px;
    transition: color 0.4s;
    &:hover {
      .header__social-descr,
      .header__social-text {
        color: var(--color-primary);
      }
    }
  }

  &__social-descr {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-mako);
    transition: color 0.4s;
  }

  &__social-text {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-black);
    transition: color 0.4s;
  }

  &__social--mail {
    background-image: url(../img/chat.svg);
  }
  &__social--tel {
    background-image: url(../img/phone.svg);
  }
  &--second {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
  }
}
.logo {
  z-index: 999;
}
.burger {
  @include burger;
  display: none;
  @include tablet {
    display: block;
    z-index: 999;
  }
}
.menu--active {
  transform: translateX(0);
}
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background-color: var(--color-white);
  transition: 0.5s;
}
