.contacts-us {
  padding: 120px 0;
  @include tablet {
    padding: 60px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }

  &__sub-title {
    text-align: center;
    margin-bottom: 60px;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }
  &__img {
    @include tablet {
      text-align: center;
      width: 100%;
      margin-bottom: 50px;
    }
  }
}
.contacts-form {
  &__content {
    display: flex;
    flex-wrap: wrap;
    max-width: 600px;
    gap: 25px 30px;
    @include tablet {
      max-width: none;
      justify-content: space-between;
    }
  }
  &__label {
    flex-basis: 45%;
    display: flex;
    flex-direction: column;
    @include mobile {
      flex-basis: 100%;
    }
  }
  &__caption {
    margin-bottom: 8px;
  }
  .choices[data-type*="select-one"]:after {
    transition: 0.4s;
  }
  &__under {
    margin-top: 40px;
    display: flex;
    gap: 75px;
    label {
      max-width: 300px;
    }
    @include small-tablet {
      flex-direction: column;
    }
  }
}
.form-fieldset {
  border: none;
  &__head {
    margin-bottom: 15px;
  }
  &__content {
    display: flex;
    gap: 25px;
  }
  &__check {
    margin-bottom: 0;
  }
}
