.offices {
  padding: 60px 0;
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }

  &__under-title {
    text-align: center;
    margin-bottom: 60px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    gap: 70px 40px;
    flex-wrap: wrap;
  }

  &__info {
    max-width: 250px;
  }

  &__info-title {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-black);
    margin-bottom: 25px;
  }

  &__info-address {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-black);
    margin-bottom: 5px;
  }

  &__info-link {
    display: inline-block;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-primary);
    border-bottom: 2px solid var(--color-primary);
    margin-bottom: 20px;
  }

  &__info-descr {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__info-value {
    color: var(--color-black);
  }
}
